.container {
	--w: 1330px;
	--p: 30px;
	max-width: var(--w);
	padding-left: var(--p);
	padding-right: var(--p);
	margin-right: auto;
	margin-left: auto;
	position: relative;
	&-fluid {
		--w: 100%;
		--p: 80px; } }


.running-line {
	--mt: -86px;
	margin-top: var(--mt);
	z-index: 9;
	position: relative;
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	& &__box {
		--fz: 140px;
		--time: 20s;
		font-size: var(--fz);
		color: #212121;
		display: inline-block;
		padding-left: 100vw;
		animation: runningText var(--time) infinite linear;
		white-space: nowrap;
		position: relative;
		&--transparent {
			color: transparent;
			.text-line {
				position: absolute;
				top: 0;
				left: 100vw;
				letter-spacing: 0.2375rem; } } } }

@keyframes runningText {
	0% {
		transform: translate(0, 0); }
	100% {
		transform: translate(-100%, 0); } }


.text-line {
	--w: 100%;
	--h: 100%;
	--color: #000000;
	--border: 2px;
	--fw: 700;
	--fz: 8.75rem;
	width: var(--w);
	height: var(--h);
	stroke: var(--color);
	stroke-width: var(--border);
	font-weight: var(--fw);
	font-size: var(--fz);
	display: flex;
	&-with-ghost {
		position: relative;
		color: transparent;
		--fz-ghost: 30px;
		font-size: var(--fz-ghost);
		.text-line {
			--t: 0;
			--l: 0;
			position: absolute;
			top: var(--t);
			left: var(--l);
			font-size: var(--fz-ghost); } } }


@keyframes dash {
	to {
		stroke-dashoffset: 0; } }


@keyframes filling {
	from {
		fill: #2A4A73;
		fill-opacity: 0; }

	to {
		fill: #2A4A73;
		fill-opacity: 1; } }




.icon-near-text {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: flex-start;
	&__icon {
		margin-right: 2.375rem;
		.icon {
			--s: 50px;
			width: var(--s);
			height: var(--s); } }
	&__wrap {}

	&__title {
		font-weight: 600;
		font-size: 1.5rem;
		margin: 0.3125rem 0 2.125rem;
		line-height: 1.4; }
	&__desc {
		color: var(--gray);
		font-weight: 400;
		line-height: 1.6; } }





.header {
	background-color: var(--gray-light);
	min-height: 67.5rem; }



.header-slide {
	padding-top: 9.125rem;
	&__meta {
		margin: 0; }
	&__title {
		font-size: 6.5625rem;
		margin: 0;
		line-height: 1.25;
		margin-bottom: 2.625rem;
		font-weight: 400;
		letter-spacing: 0.09375rem; }
	&__btn {} }




.block-title {
	font-size: 5.75rem;
	font-weight: 700;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: flex-start;
	margin-top: var(--typo-elm-mt);
	margin-bottom: var(--typo-elm-mb);
	& &__icon {
		line-height: 1;
		.text-line {
			--border: 0.0625rem;
			--color: var(--pr);
			--fz: 2.25rem; } }
	& &__text {
		margin-left: -0.25rem;
		letter-spacing: -0.0625rem;
		&-line {
			--fz-ghost: 5.75rem;
			margin-top: -0.125rem;
			margin-left: -0.3125rem;
			letter-spacing: -0.0625rem;
			.text-line {
				--border: 1px; } } } }

.title-cols {
	.block-title-wrap {
		.block-title {
			--typo-elm-mt: 0;
			--typo-elm-mb: 3.75rem; } }
	.block-text-wrap {
		.text-mutted {} } }







.partners {
	& &__box {
		justify-content: space-between; }
	& &__text {}
	& &__btn-holder {
		align-self: flex-end;
		margin-bottom: 2.25rem; } }




.services {
	& &__wrapper {
		margin-top: 11.625rem; }
	& &__item {
		margin: 5.9375rem 0;
		&-title {
			font-size: 4.375rem;
			margin: 0;
			cursor: pointer;
			&.text-mutted {
				font-size: 2rem;
				font-weight: 400; } } } }



.quotation {
	background-color: var(--gray-light);
	min-height: 1080px;
	.container {
		padding-top: 15rem; }
	& &__box {
		position: relative;
		padding: 8.4375rem 0 0;
		margin-left: 10.25rem;
		.icon-quote {
			--w: 7.875rem;
			--h: 7.875rem;
			position: absolute;
			top: 0;
			left: 0;
			color: var(--pr); }
		.icon-hero-element {
			--w: 9rem;
			--h: 9rem;
			position: absolute;
			bottom: -7.1875rem;
			right: -7.8125rem; } }
	& &__quote {
		margin: 0 0 1.5rem 10.5rem;
		&-text {
			font-size: 2.5rem;
			font-weight: 600;
			margin: 0; } }
	& &__caption {
		display: flex;
		flex-direction: column;
		line-height: 1.65;
		&-top {
			font-size: 1.125rem;
			font-weight: normal;
			font-weight: 700; }
		&-bottom {
			font-size: 0.875rem;
			color: var(--gray);
			font-weight: 400; } }
	.detail {
		&.detail-1 {
			--t: -6.5rem;
			--r: -27.5rem;
			--sz: 29.8125rem;
			--rt: -2deg; }
		&.detail-2 {
			--b: -15.625rem;
			--l: -31.625rem;
			--sz: 35.625rem;
			--rt: 87deg; } } }



.gallery-mega-slide {
	background-color: var(--gray-light);
	padding: 5rem 5rem 3.75rem 5rem;
	min-height: 40.625rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin-bottom: 2.625rem;
	& &__logo {
		max-width: 14.375rem;
		max-height: 3.75rem;
		&-wrap {} }
	& &__img {
		width: 100%;
		max-width: 23.75rem;
		&-wrap {
			position: absolute;
			bottom: -3rem;
			right: -1.25rem; } }
	& &__text {
		font-size: 1.25rem;
		&-wrap {
			max-width: 33.75rem; } }
	& &__btn {
		margin-top: 2.5rem; } }


.gallery {
	& &__box {
		justify-content: space-between;
		margin-bottom: 6.75rem; }
	& &__btn-holder {
		align-self: flex-end;
		margin-bottom: 6.25rem; }
	& &__meta-item {
		font-size: 1rem;
		color: var(--gray-light2); } }



.video-block {
	--min-h: 1080px;
	--img: url('../img/video-img.jpg');
	min-height: var(--min-h);
	background-image: var(--img);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	& &__play {
		--fz: 0.75rem;
		--sz: 5.125rem;
		--bg-color: var(--pr);
		--t-style: none;
		--t: 13.125rem;
		--r: 25.9375rem;
		position: absolute;
		top: var(--t);
		right: var(--r); } }



.join-team {
	background-color: var(--gray-light);
	.blog-card {
		.blog-card__category {
			--fz: 1.25rem;
			--mb: -0.625rem; } }
	.block-title {
		&__text {
			&-line {
				.text-line {
					width: 110%; } } } }
	.form-style {
		margin-top: 5rem; }
	.detail {
		&.detail-1 {
			--t: -8.875rem;
			--r: -28.75rem;
			--sz: 20.625rem;
			--rt: -192deg; }
		&.detail-2 {
			--b: -12.75rem;
			--l: -31.625rem;
			--sz: 24.375rem;
			--rt: 127deg; } } }
